import { Box, Fade, Popover, Popper } from "@mui/material";
import { useEffect, useState } from "react";
import "../../components/app-popover/popover-component.scss"

interface PopoverContentProps {
    open: boolean;
    anchorEl: HTMLElement | null;
    closePopover: () => void;
    configData: Options[];
    selectedData: (ev: Options) => void;
}

export interface Options {
    id: number;
    text: string;
}


const PopoverComponent: React.FC<PopoverContentProps> = ({ open, anchorEl, closePopover, configData, selectedData }) => {
    const [menuOption, setMenuOption] = useState<Options[]>([{
        id: 0,
        text: ''
    }])

    useEffect(() => {
        setMenuOption(configData)
    }, [])

    const handleItemClick = (option: Options) => {   
        selectedData(option);
        closePopover()
    };


    return (
        <Popover
            id={'id'}
            open={open}
            anchorEl={anchorEl}
            onClose={closePopover}
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
            }}
            classes = {{paper: 'popover_content'}}
        >{

            }
            <div className="moreOptions">
                <ul className="list-Container">
                    {menuOption.map((option: Options, index: number) => {
                        return <li className="list-item"key={index} onClick={() => handleItemClick(option)}>{option.text}</li>
                    })}
                </ul>

            </div>
            
        </Popover>
    );

};

export default PopoverComponent;