import { User } from "../_models/user";
import http from "../_core/helpers/axios/axios-instance";
import { ReportAttachments, ReportDetail, ReportedList } from "../_models/report";

const GET_USER = '/user/me';
const GET_REPORTED_LIST = '/support/reports';
const GET_REPORT_DETAILS = '/support/reports/:reportId'
const USER_WITH_ID = `/user/:id`;
const GET_POST_BY_ID = '/support/reports/:reportId';
const SAVE_REPORT = '/support/reports';
const GET_COMMENTS = '/comments';
const GET_USERS_WITH_ID = '/user/list';

const getUser = () => {
    return http.get<Array<User>>(GET_USER);
};

const getReportList = (status: number, searchTerm?: string) => {

    interface Params {
        status: number;
        searchTerm?: string | null;
    }

    const params: Params = {
        status
    }

    if (searchTerm) params.searchTerm = searchTerm;
    return http.get<ReportedList[]>(GET_REPORTED_LIST, { params })
}

const fetchDetailsByReportId = async (reportId: number) => {
    let url = GET_POST_BY_ID.replace(':reportId', reportId.toString());
    return http.get<ReportDetail>(url);
}

const saveReport = async (reportId: number, attachment: ReportAttachments) => {
    const attachments: ReportAttachments = { action: attachment.action }
    if (attachments.comments) attachment.comments = attachments.comments
    return http.put<ReportDetail>(SAVE_REPORT, {
        reportId,
        attachments
    });
}

const getCommentsByPostId = async (postId: number) => {
    return http.get(GET_COMMENTS, {
        params: { postId }
    });
}

const getUserById = (userId: number) => {
    if (userId) {
        let url = USER_WITH_ID.replace(':id', userId.toString());
        return http.get<any>(url)
    }
}

const getUserList = (payload: number[]) => {
    return http.post<User[]>(GET_USERS_WITH_ID, { 'ids': payload })
}

const UserService = {
    getUser,
    getReportList,
    fetchDetailsByReportId,
    saveReport,
    getCommentsByPostId,
    getUserById,
    getUserList
};

export default UserService;
