import React, { useEffect, useMemo, useState } from 'react';
import './comments-viewer.scss'
import UserService from '../../../_service/user-api';
import { Logger } from '../../../_core/logger.service';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../redux/root-reducer';
import { getCommentsForPost } from '../../../redux/feature/flagged-report-slice';
import { Avatar, Typography } from '@mui/material';
import { Comments, InitialComments } from '../../../_models/report';
import likes from '../../../../assets/icons/likes.svg';
import { RootReducer } from '../../../_models/roo-reducer-model';

const logger = new Logger();

interface PropsForComments {
  postId: number;
}

const CommentsViewer = (props: PropsForComments) => {
  const [count, setCount] = useState(0);
  // const detailedObj: any = useSelector((state: RootReducer) => state.flaggedReport.data);
  // const comments = useSelector((state: any) => state.flaggedReport.commentsDetails);
  // const dispatch = useDispatch();
  // const { post } = detailedObj;
  const [comments, setComments] = useState<Comments[]>([InitialComments])

  // useMemo(() => {
  //   UserService.getCommentsByPostId(post.id)
  //     .then((res) => {
  //       dispatch(getCommentsForPost(res))
  //     })
  //     .catch(err => logger.error(err))
  // }, [post.id])

  useEffect(() => {
    fetchComments(props.postId);
  }, [props.postId])

  function fetchComments(postId: number) {
    UserService.getCommentsByPostId(postId)
      .then((res: any) => {
        setComments(res);
        const cCount = countTotalObjects(comments);
        setCount(cCount);
      })
      .catch((err) => {
        logger.error(err);
      })
  }

  const countTotalObjects = (comments: Comments[]) => {
    let count = comments.length; // Count the current level objects
    if (comments && comments.length) {
      comments?.forEach((comment: Comments) => {
        if (comment.children && comment.children.length > 0) {
          count += countTotalObjects(comment.children); // Recursively count nested objects
        }
      });
    }
    return count;
  }

  return (
    <>
      {comments.length ? comments.map((comment: Comments, index: number) => {
        return <div key={index}>
          {commentsThread(comment, index)}
        </div>
      }) : ''}
    </>
  )
}

const commentsThread: any = (comments: Comments, index: number) => {
  return (
    <>
      <div className='comment_container' key={index}>
        <div style={{ marginLeft: `${(comments.level - 1) * 50}px` }} className='info_pic'>
          <Avatar alt={comments.name} src={comments.profilePic} />
          <div className='info_text_comment'>
            <Typography className='semiBold-16 comment_user_name'>{comments.display_name ?? comments.name}</Typography>
            <Typography className='regular-16'>{comments.content}</Typography>
            <div className='likes'>
              <Avatar sx={{ width: '13px', height: '13px', marginTop: '-4px' }} alt='likes' src={likes}></Avatar>
              <Typography className='regular-14 likes_info'>0 Likes</Typography>
            </div>
          </div>
        </div>
        {
          comments.children?.map((el: Comments, index: number) => {
            return <div key={index}>
              {commentsThread(el, index)}
            </div>
          })
        }
      </div>
    </>
  )
}

export default CommentsViewer
