import React, { useEffect, useState } from 'react'
import '../image-previewer/image-previewer.scss';
import { Attachments, Post, initializeAttachments } from '../../../_models/report';
import arrow from '../../../../assets/icons/image_arrow.svg';
import { PostFormat } from '../../../enum/ReportEnum';
import PlayCircleOutlinedIcon from '@mui/icons-material/PlayCircleOutlined';
import { Tooltip } from '@mui/material';
import LocalOfferOutlinedIcon from '@mui/icons-material/LocalOfferOutlined';
import LocationOnOutlinedIcon from '@mui/icons-material/LocationOnOutlined';
import AudiotrackOutlinedIcon from '@mui/icons-material/AudiotrackOutlined';
import AppModelService from '../../../_service/app-model-service';


interface PropsForImagePreviewer {
    attachments: Attachments[] | [];
    click?: () => void;
    taggedPeopleTap: (userId: number[]) => void;
    post?: Post;
}

const ImagePreviewer = (props: PropsForImagePreviewer) => {
    const [currentIndex, setCurrentUser] = useState<number>(0);
    const [side, setSide] = useState<number>(0);
    const [postInfo, setPostInfo] = useState<Attachments[]>([
        initializeAttachments
    ]);
    const [locationTooltip, setLocationTooltip] = useState<boolean>(false);
    const [songTooltip, setSongTooltip] = useState<boolean>(false);

    useEffect(() => {
        setPostInfo(props.attachments);
    }, [props.attachments])

    const handleChange = (side: number) => {
        setSide(side);
        setCurrentUser(currentIndex + side);
    }

    const imageClick = () => {
        if (props.click) {
            setLocationTooltip(false);
            setSongTooltip(false);
            props.click();
        }
    }

    function tagPeopleClick(params: number[]) {
        setLocationTooltip(false);
        setSongTooltip(false);
        props.taggedPeopleTap(params);
    }

    function locationToolTipTapped() {
        setLocationTooltip((prev) => !prev);
        setSongTooltip(false);
    }

    function songToolTipTapped() {
        setSongTooltip((prev) => !prev);
        setLocationTooltip(false);
    }

    return (
        <>
            <div className='image_preview_container'>
                {
                    postInfo?.length ? postInfo.map((source: Attachments, index: number, originalArray: Attachments[]) => {
                        return index === currentIndex && source.url &&
                            <div className='main' key={index}>
                                {
                                    originalArray.length > 1 && index !== 0 && (
                                        <div className='left_arrow' onClick={(ev) => handleChange(-1)}>
                                            <img className='arrow_icon' src={arrow} alt="arrow" />
                                        </div>
                                    )
                                }

                                <img onClick={() => imageClick()}
                                    className={`${side === 1 ? 'forward' : side === -1 ? 'backward' : 0} source_container`}
                                    key={index}
                                    src={originalArray.at(currentIndex)?.type === PostFormat.image ? originalArray.at(currentIndex)?.url : originalArray.at(currentIndex)?.thumbnailUrl}
                                    alt="Post image not found"
                                />

                                {
                                    originalArray.at(currentIndex)?.type === PostFormat.video ?
                                        <Tooltip title='Click to play video'>
                                            <PlayCircleOutlinedIcon className='play_icon' />
                                        </Tooltip>
                                        : ''
                                }

                                {
                                    originalArray.length > 1 && index < originalArray.length - 1 ? (
                                        <div className='right_arrow' onClick={(e) => handleChange(1)}>
                                            <img className='arrow_icon' src={arrow} alt="arrow" />
                                        </div>
                                    ) : ''
                                }
                                <div className='actions-main-container'>
                                    {
                                        props.post?.taggedPeople &&
                                        <div className='tag-container' onClick={() => tagPeopleClick([206, 69, 67])}>
                                            <LocalOfferOutlinedIcon />
                                        </div>
                                    }

                                    {
                                        props.post?.taggedLocation &&
                                        <div className='location-container' onClick={locationToolTipTapped}>
                                            <Tooltip
                                                PopperProps={{
                                                    disablePortal: true,
                                                }}
                                                onClose={() => setLocationTooltip(false)}
                                                open={locationTooltip}
                                                disableFocusListener
                                                disableHoverListener
                                                disableTouchListener
                                                title={props.post?.taggedLocation.mainText}
                                            >
                                                <LocationOnOutlinedIcon />
                                            </Tooltip>
                                        </div>
                                    }

                                    {
                                        props.post?.previewSong &&
                                        <div className='song-container' onClick={songToolTipTapped}>
                                            <Tooltip
                                                PopperProps={{
                                                    disablePortal: true,
                                                }}
                                                onClose={() => setSongTooltip(false)}
                                                open={songTooltip}
                                                disableFocusListener
                                                disableHoverListener
                                                disableTouchListener
                                                title={props.post.previewSong.name}
                                            >
                                                <AudiotrackOutlinedIcon />
                                            </Tooltip>
                                        </div>
                                    }
                                </div>
                            </div>
                    }) : ''
                }
            </div>
        </>
    )
}

export default ImagePreviewer
