import './report-view.scss';
import likes from '../../../../assets/icons/likes.svg';
import comments from '../../../../assets/icons/comments.svg';
import shares from '../../../../assets/icons/repost.svg';
import arrow from '../../../../assets/icons/arrow.svg';
import smiley from '../../../../assets/icons/smiley.svg';
import three_dots from '../../../../assets/icons/three_dots.svg';
import { ChangeEvent, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../redux/root-reducer';
import { getReportDetail } from '../../../redux/feature/flagged-report-slice';
import { Attachments, Post, PreviewSong } from '../../../_models/report';
import { Query } from '../../../_models/user';
import { Avatar, Box, Card, FormControl, FormControlLabel, Grid, Popover, Popper, Radio, RadioGroup, Typography } from '@mui/material';
import ImagePreviewer from '../image-previewer/image-Previewer';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import CommentsViewer from '../comments-viewer/comments-viewer';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import AppModelService from '../../../_service/app-model-service';
import ProfileDetails from '../app-profile-details/profile-details';
import PopoverComponent, { Options } from '../app-popover/popover-component';
import AppPreviewModelService from '../../../_service/app-preview-model-service';
import { Navigate } from 'react-router';
import { DataTypes } from '../../../enum/data-types';
import { PostTypes } from '../../../enum/ReportEnum';
import PostDetailedView from '../app-post-detailed-view/post-detailed-view';
import { RootReducer } from '../../../_models/roo-reducer-model';

const postInfo = [
    { key: 'likes', text: 'likes', source: likes },
    { key: 'comments', text: 'comments', source: comments },
    { key: 'shares', text: 'repost', source: shares }
];

const configData = [{ id: 1, text: 'View profile' }, { id: 2, text: 'Chat' }]

interface viewProps {
    onChangeOption: (query: Query) => void;
    warningElementChange: (value: string) => void;
    isRender: boolean;
    taggedPeopleTap: (userId: number[]) => void;
}

const ReportView = (props: viewProps) => {
    const [selectedOption, setSelectedOption] = useState('');
    const [inputValue, setInputValue] = useState('');
    const detailedObj: any = useSelector((state: RootReducer) => state.flaggedReport.data);
    const { user, query, reportedUser, post, actionList, attachments, detail } = detailedObj;
    const [pAnchorEl, setpAnchorEl] = useState<EventTarget | any>(null);
    const [popoverOpen, setPopoverOpen] = useState(false);

    useEffect(() => {
        if (props.isRender) {
            setSelectedOption('');
            props.warningElementChange('');
            setInputValue('');
        }
    }, [props.isRender])

    const labelClick = (event: React.ChangeEvent<HTMLInputElement>) => {
        setSelectedOption(event.target.value);
        props.onChangeOption(actionList[parseInt(event.target.value)]);
    }

    const onChangeinput = (event: ChangeEvent<HTMLTextAreaElement>) => {
        props.warningElementChange(event.target.value?.trim());
        setInputValue(event.target.value);
    }

    const openAction = (event: React.MouseEvent<HTMLElement>) => {
        setPopoverOpen(true);
        setpAnchorEl(event.currentTarget);
    }

    const closePopover = () => {
        setPopoverOpen(false);
    }

    const selectedData = (ev: Options) => {
        AppModelService.createPopUp({
            compoent: <ProfileDetails id={user.id} />,
            headerName: '',
            header: false,
            footer: false,
            width: 580,
            height: 560,
            class: 'main_content',
            showCloseIcon: true
        })
    }

    const closePreviewPopover = () => {
        console.log("Preview popup closed..!");
    }

    const imageClick = (attachments: Attachments[], previewSong: PreviewSong | null) => {
        AppPreviewModelService.createPreviewPopUp({
            headerName: "",
            attachments: attachments,
            previewSong: previewSong
        }, closePreviewPopover);
    }

    const showRepostDetail = (postInfo: Post) => {
        AppModelService.createPopUp({
            compoent: <PostDetailedView post={postInfo} />,
            width: 1020,
            height: 560,
            header: false,
            headerName: '',
            footer: false,
            class: 'main_content no_padding',
            showCloseIcon: false
        });
    }
    const taggedPeopleTapped = (params: number[]) => {
        props.taggedPeopleTap(params);
    }

    function showPreviewPopup(attachments: Attachments[]) {
        AppPreviewModelService.createPreviewPopUp({
            headerName: "",
            attachments: attachments,
        });
    }

    return (
        <>
            <div className='reportView'>
                <div className='postContent'>
                    <div className='profile'>
                        <div className='detail'>
                            {/* <img className='profileIcon' src={user.profilePic} alt={user?.name} /> */}
                            <Avatar className='profileIcon' src={user.profilePic} alt={user?.name}></Avatar>
                            <p className='name regular-18'>{user?.name}</p>
                        </div>
                        <div onClick={(e) => openAction(e)}>
                            <MoreVertIcon className='three_dots' />
                        </div>
                    </div>
                    <div className='postCell'>
                        <div className='report_detail_container'>
                            <p className='option regular-18'>
                                <span className='primary_text_color'>Query: </span>
                                <span className='quinary_text_color'>{query.question}</span>
                            </p>
                            {
                                detail &&
                                <div>
                                    <label>
                                        <span className='primary_text_color'>Description: </span>
                                        <span className='quinary_text_color'>{detail}</span>
                                    </label>
                                </div>
                            }
                            {
                                attachments &&
                                <div>
                                    <label className='attachment_label' onClick={() => showPreviewPopup(attachments)}>Show Attachments</label>
                                </div>
                            }
                        </div>
                        <div className='source'>
                            <div className='owner-profile'>
                                {/* <img width={'30px'} height={'30px'} src={post.user?.profilePic} alt={post.user?.displayName ?? post.user?.name} /> */}
                                <Avatar sx={{ width: 30, height: 30 }} src={post.user?.profilePic} alt={post.user?.displayName ?? post.user?.name}></Avatar>
                                <p className='medium-18 font-color'>{post.user?.displayName ?? post.user?.name}</p>
                            </div>
                            {
                                typeof (post) === DataTypes.object ? (
                                    <div className={`${post.type === PostTypes.social ? 'not_card' : 'card_height'} post-image`}>
                                        <Card variant="elevation" className={`${post.type === PostTypes.social ? 'not_a_card' : 'card'}`}>
                                            {
                                                post.type === PostTypes.repost ?
                                                    <Grid container direction={"row"} justifyContent={"space-between"} alignItems={"center"} padding={'15px 15px 10px 25px'}>
                                                        <Grid item display={"flex"} flexDirection={"row"} alignItems={"center"} columnGap={"10px"}>
                                                            <Avatar sx={{ width: 30, height: 30 }} src={post.data?.user?.profilePic}></Avatar>
                                                            <Typography className='medium-18'>{post.data?.user?.displayName ?? post.data?.user?.name}</Typography>
                                                        </Grid>
                                                        <Grid item>
                                                            <span onClick={() => showRepostDetail(post.data)} >
                                                                <Typography className='medium-18 link_text'>show post</Typography>
                                                            </span>
                                                        </Grid>
                                                    </Grid> : ''
                                            }
                                            {post.attachments?.length || post.data?.attachments?.length ?
                                                <ImagePreviewer
                                                    click={() => imageClick(post.attachments.length ? post.attachments : post.data?.attachments, post.previewSong ?? post.data?.previewSong ?? null)}
                                                    taggedPeopleTap={(params) => taggedPeopleTapped(params)}
                                                    attachments={post.attachments.length ? post.attachments : post.data?.attachments}
                                                    post={post}
                                                />
                                                : ''}
                                        </Card>
                                        <div className='details'>
                                            <div className='sticky_content'>
                                                <div className='thoughts'>
                                                    <p className='regular-18 font-color'>
                                                        {post.name}
                                                    </p>
                                                </div>
                                                <div className='post-info'>
                                                    {
                                                        post.attachments?.length || post.data ?
                                                            postInfo.map((info: any, index: number) => (
                                                                <div className='info' key={index}>
                                                                    <img src={info.source} alt={info.text} />
                                                                    <p className='regular-16 count'>{`${detailedObj[info.key]} ${info.text}`}</p>
                                                                </div>
                                                            ))
                                                            : ''
                                                    }

                                                </div>
                                            </div>
                                            {/* <div className='justify-center'>
                                                <p className='semiBold-18 name' style={{ textAlign: 'center', cursor: 'pointer' }}>Show post detail</p>
                                            </div> */}
                                            {post.name && <div className='comments_viewer_flag'>
                                                <CommentsViewer postId={post.id} />
                                            </div>}
                                        </div>
                                    </div>
                                ) : (
                                    <div className='error_text'>
                                        <p className='regular-16 info_text'>Post Information not found</p>
                                        <ErrorOutlineIcon />
                                    </div>

                                )
                            }

                        </div>

                    </div>
                </div>
                <div className='action'>
                    <p className='label semiBold-18'>Take action:</p>
                    <div className='action-option'>
                        <FormControl>
                            <RadioGroup aria-labelledby="Report actions"
                                name="radio-buttons-group" value={selectedOption?.toString()}
                                onChange={labelClick}>
                                {
                                    actionList?.length &&
                                    actionList.map((option: any, index: number) => (
                                        <FormControlLabel className='radio' key={index} value={index.toString()} control={<Radio />}
                                            label={option.question}></FormControlLabel>
                                    ))
                                }
                            </RadioGroup>
                        </FormControl>
                    </div>
                    <div className='warning-input'>
                        {
                            selectedOption === '1' ?
                                <div>
                                    <p className='input-label semiBold-18 font-color'>Issue warning to the post owner: </p>
                                    <div className='input-container'>
                                        <textarea placeholder='Provide valid information...' className='inputBox' name="input"
                                            value={inputValue} onChange={onChangeinput}></textarea>
                                        <img className='smiley' src={smiley} alt="smiley" />
                                        <img src={arrow} alt="arrow" />
                                    </div>
                                </div>
                                : ''
                        }
                    </div>
                </div>
            </div>
            <PopoverComponent open={popoverOpen} anchorEl={pAnchorEl} closePopover={closePopover} configData={configData} selectedData={selectedData} />
        </>
    )
}

export default ReportView;
