import React from 'react';
import './segment.scss';
import { SegmentModel } from '../../../_models/report';

interface SegmentProps {
    segments: SegmentModel[];
    onSegmentClick: (index: number) => void;
}

const SegmentControl: React.FC<SegmentProps> = ({ onSegmentClick, segments }) => {
    return (
        <div className='segment-background'>
            {segments.map((segment, index) => (
                <button className={segment.isSelected ? 'segment-button' : 'segment-button-inactive'}
                    key={index}
                    onClick={() => onSegmentClick(index)}
                >
                    <div className={`${segment.isSelected ? 'segment-label' : 'segment-label-inactive'} semiBold-18`}>{segment.name}</div>
                    {/* <div className={`${segment.isSelected ? 'segment-count' : 'segment-count-inactive'} section-header`}>{segment.count ?? ''}</div> */}
                </button>
            ))}
        </div>
    );
};
export default SegmentControl;