import { User } from "../../../_models/user";
import '../../components/user-profile-card/user-profile-card.scss';

interface UserProfileCard {
    userList: User[]
}

const UserProfileCard: React.FC<UserProfileCard> = ({ userList }) => {
    return (
        <>
            {
                userList?.map((item, index) => (
                    <div key={index} className='list-items'>
                        <div className='list-content'>
                            <img className='user-list-profile' src={item.profilePic}></img>
                            <div className="query-container">
                                <label className='regular-18 font-color'>{item.displayName ?? item.displayName}</label>
                            </div>
                        </div>
                    </div>
                ))
            }
        </>
    );
}

export default UserProfileCard;
